.utbf-form__row {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  margin-bottom: 10px;
}
.utbf-form__full-col {
  flex: 1;
  width: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}
.utbf-form__half-col {
  flex: 1;
  width: 50%;
  display: flex;
  flex-direction: column;
  position: relative;
}
@media (max-width: 576px) {
  .utbf-form__half-col {
    width: 100%;
  }
}

input.text,
input.title,
input[type=email],
input[type=password],
input[type=tel],
input[type=text],
select,
textarea {
  color: #6e6e6e;
  border: 1px solid #ccc;
  padding: 10px 5px;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -ms-border-radius: 3px;
  -o-border-radius: 3px;
}

.button {
  font-size: 16px;
  padding: 7px 20px;
  font-weight: 600;
  text-transform: uppercase;
  cursor: pointer;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -ms-border-radius: 3px;
  -o-border-radius: 3px;
}
.button-primary {
  color: #ffffff;
  background-color: #2EA3F2;
}
.button-large {
  padding: 10px 50px;
}

.et_fb_custom_register_form::before {
  content: "" !important;
  background-image: url("../img/register.svg") !important;
  height: 16px;
  background-position: 50%;
  background-size: contain;
  background-repeat: no-repeat;
}